                font-size: 1.5rem;
              }
            }
          }
        }
        .right-h {
          position: absolute;
          top: 4rem;
          right: 1.5rem;

          .header_social {
            position: absolute;
            right: 4rem;
            font-size: 1rem;
            top: 25rem;
          }

          .me {
            border-radius: 12rem 12rem 0 0;
            width: 5rem;

